const SUMMARY_FIELDS = [
  { key: "totalCount", label: "Total" },
  { key: "activeCount", label: "Active" },
  { key: "completedCount", label: "Completed" },
  { key: "pendingJiraCount", label: "Pending Jira" },
  { key: "lotCount", label: "Lots" },
]

export function LineSummaryPanel({ summary }) {
  if (!summary) return null

  return (
    <section className="grid grid-cols-2 gap-4 rounded-lg border bg-muted/30 p-4 sm:grid-cols-3 lg:grid-cols-5">
      {SUMMARY_FIELDS.map(({ key, label }) => (
        <div key={key} className="flex flex-col gap-1">
          <span className="text-xs font-medium uppercase tracking-wide text-muted-foreground">
            {label}
          </span>
          <span className="text-xl font-semibold">
            {summary[key] ?? "-"}
          </span>
        </div>
      ))}
    </section>
  )
}
